.App {
  font-family: sans-serif;
  text-align: center;
  font-size: 12px;
}

table {
  border: 1px solid #bec1c4;
  border-collapse: collapse;
  width: 100%;
}

table th {
  padding: 10px 0;
  background: #bec1c4;
}

table td {
  border-right: 1px solid #bec1c4;
  border-left: 1px solid #bec1c4;
  border-bottom: 1px solid #bec1c4;
}
table .day .header {
  text-align: center;
  padding: 10px 0;
  font-size: 11px;
  border-bottom: 1px solid #eeeeee;
  /* background: #dee2e6; */
}
table .day .icon {
  padding: 15px 5px 0;
  opacity: 0;
}
table .day .icon img {
  width: 20px;
}

table .day .people {
  display: block;
  padding: 10px 10px;
  min-height: 14px;
  /* font-style: italic; */
}

table td.active {
  background: #eeeeee;
  font-weight: bold;
}

table td.active .icon {
  opacity: 1;
}

table td.past {
  opacity: 0.2;
}

table td.selected {
  background: #badaf7;
}

#swap-container {
  opacity: 0;
  transition-property: opacity;
  transition-duration: 250ms;
  background: #ffffff;
  border-top: 1px solid #bec1c4;
  min-height: 150px;
  width: 100%;
}

#swap-container.open {
  opacity: 0.9;
}

#swap-container .slot {
  width: 100%;
  height: 120px;
  border: dotted 2px #bec1c4;
  border-radius: 10px;
}

#swap-container .slot .ant-flex {
  height: 100%;
}

#swap-container .slot .person {
  padding: 10px;
  font-weight: bold;
}
